*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
h1, h2, h3, p{
  margin: 0;
  padding: 0;
}

h1, h2, h3{
  font-size: 16px;
}
img{
  width: 100%;
}
.App {
  font-size: 14px;
  width: 100%;
    padding: 0;
    margin: 0;
  .container{
     max-width: 500px;
      margin: 0 auto;
    position: relative;
    padding: 0;
    .home{
      padding-bottom: 7em;
      .main_head{
        width: 100%;
        padding: 8px 5em;
        background-color: #2766CC;
        color: #fff;
      }
      .home_header{
        padding: 1em;    
        .logo{
          display: flex;
          align-items: center;
          img{
            width: 40px;
          }
          .header_text{
            font-size: 1em;
            padding: 0 1em;
          }
        } 
      }
    }
    .bag{
      padding-bottom: 8em;
      max-width: 600px;
      margin: 0 auto;
      .bag_header{
        padding: 1em;
        border-bottom: 2px solid #E5E5E5;
        font-weight: 800;
        position: relative;
        .fa-shopping-cart{
          font-size: 20px;
          width: 25px;
          margin-left: 10px;
        }
        span{
          background-color: #D6001C;
          color: #fff;
          padding: 2px 10px;
          border-radius: 4px;
          margin-top: -10px;
         
        }
      }
      .carts_items{
        padding: 1em;
        .cart{
            display: flex;
            justify-content: space-between;
            margin: 1em 0;
            padding: 1em 0;
            .cart_box1{
              width: 60%;
              display: flex;
              justify-content: space-between;
              align-items: center;
              .cart_img{
                width: 50%;
               img{
                border-radius: 10px;
               }
              }
              .cart_text{
                
                p{
                  margin-top: 0.5em;
                }
              }  
            }
            .btn_wrapper{
              align-self: flex-end;
            }
        }
      }
      .info{
        text-align: center;
        img{
          width: 60px;
          margin: 1em 0;
        }
      }
      .invoice{
        padding: 2em;
        display: flex;
        justify-content: space-between;
        p{
          margin-top: 0.4em;
        }
        .price{
          p{
              font-weight: 700;     
          }
        }
      }
      .total{
        padding: 0em 2em;
        display: flex;
        justify-content: space-between;
        font-weight: 900;
      }
      button{
          background-color: hsl(120, 80%, 19%);
          color:#fff ;
          border: none;
          width: 80%;
          margin: 2em;
          padding: 8px;
          outline: none;
        
      } 
    }
    nav{
      width: 100%;
      margin: 0 auto;
      display: flex;
      justify-content:space-evenly ;
      position: fixed;
      bottom: 0;
      background-color: #FFFFFF;
      z-index: 100;
      a{
        width: 50%;
        text-decoration: none;
        .active{
          background-color: #D6001C;
          color: #fff;
        }
        .noactive{
          color: #B3B3B3;
        }
        div{
          width: 100%;
          padding: 0.5em;
          border-top: 2px solid ;
          color: #B3B3B3;
          position: relative;
          text-align: center;
          i{
            font-size: 30px;
            width: 40px;
          }
          .cart_count{
            position: absolute;
            background-color: red;
            border-radius: 50px;
            padding:2px 6px;
            top: 5px;
          }
        }
      }
    }
    footer{
      padding: 1em;
      text-align: center;
      h3{
        margin: 0.5em 0;
      }
      .whatsapp{
        padding: 5px;
        background-color: #3fc250;
        color: #fff;
        width: 150px; 
        margin: 1em auto;
        .fa-whatsapp{
          font-size: 20px;
         width:30px;
        }
      }
    }

    .checkout{
      text-align: center;
      padding: 2em;
      padding-bottom: 7em;
      form{
        width: 90%;
        margin: 0 auto;
        input:not([type = submit]){
          width: 100%;
          margin: 0.5em 0;
          border: none;
          border-radius: 5px;
          outline: none;
          background-color: rgba(119, 119, 119,0.1);
          padding: 1.5em;
        }
        input[type = submit]{
          width: 100px;
          border-radius: 5px;
          padding: 5px 8px;
          border: none;
          outline: none;
          margin: 1em auto;
          display: block;
          background-color: hsl(120, 80%, 19%);
          color: #fff;
          animation-name: scalling;
          animation-duration: 3s;
          animation-iteration-count: infinite;
        }
        @keyframes scalling {
          0% { transform: scale(1);}
          50% { transform: scale(1.3);}
          100% { transform: scale(1);}
        }
      }
    }
    .success{
      text-align: center;
      padding: 2em;
      background-color: #F5F5F5;
      padding-bottom: 10em;
      h1{
        font-size: 24px;
        font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
        color: #57D300;
      }
    }
  }
}

@import "./mediaquery"
